  font: var(--high-bold);
  font-size: 30px;
  margin: 0;
  padding-bottom: 60px;
  text-align: center;
}
.repair-list {
  display: flex;
  justify-content: center;
  align-items: center;
  font: var(--middle-regular);
  font-size: 24px;
  line-height: 1.5;
  padding-top: 60px;
}
@media (max-width: 768px) {
  .repair-list {
    flex-direction: column;
  }
}
.repair-item {
  text-align: center;
}
