.bikes {
    left: 0;
    top: 0;
    padding-bottom: 50px;
    background-color: var(--color-white);
    &-title {
        margin: 0 auto;
	    color: var(--color-black);
        font: var(--high-bold);
	    text-align: center;
	    max-width: 800px;
	    padding: 40px 0;
    }
    &-descr {
        text-transform: uppercase;
        text-align: center;
        font: var(--high-bold);
        color: var(--color-black);
        font-size: 24px;
        margin: auto;
    }
    &-img {
        display: block;
	    margin-left: auto;
	    margin-right: auto;
        max-height: 300px;
        transition: all 1s ease-out;
    }
    &-img:hover {
        transform: scale(1.1);
        @media(max-width: 800px) {
            transform: none;
        }
    }
    &-title-pink {
        color: var(--color-pink);
    }
    &-list {
        display: grid;
	    grid-template-columns: repeat(2, 3fr);
	    gap: 30px;
	    padding-bottom: 40px;
        @media(max-width: 800px) {
            display:none;
        }
    }
    &-item {
        display: flex;
	      flex-direction: column;
    }
    &-swiper {
        @media(min-width: 801px) {
            display:none;
        }
    }
    &-button {
        color: var(--color-pink);
    }
}